<template>
  <div id="home">
    <b-row class="m-0">
      <b-col cols="12">
        <p>
          Login at :
          <span v-show="isInClient == false"
            ><svg
              xmlns="http://www.w3.org/2000/svg"
              width="30"
              height="30"
              fill="#6495ED"
              class="bi bi-laptop"
              viewBox="0 0 16 16"
            >
              <path
                d="M13.5 3a.5.5 0 0 1 .5.5V11H2V3.5a.5.5 0 0 1 .5-.5h11zm-11-1A1.5 1.5 0 0 0 1 3.5V12h14V3.5A1.5 1.5 0 0 0 13.5 2h-11zM0 12.5h16a1.5 1.5 0 0 1-1.5 1.5h-13A1.5 1.5 0 0 1 0 12.5z"
              /></svg
          ></span>
          <span v-show="isInClient == true"
            ><svg
              xmlns="http://www.w3.org/2000/svg"
              width="30"
              height="30"
              fill="#6495ED"
              class="bi bi-phone-fill"
              viewBox="0 0 16 16"
            >
              <path
                d="M3 2a2 2 0 0 1 2-2h6a2 2 0 0 1 2 2v12a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V2zm6 11a1 1 0 1 0-2 0 1 1 0 0 0 2 0z"
              /></svg
          ></span>
        </p>
      </b-col>
      <b-col cols="12">
        <p>isInClient : {{ isInClient }}</p>
        <p v-show="isInClient == true">os : {{ os }}</p>
        <p v-show="isInClient == true">language : {{ language }}</p>
        <p>version : {{ version }}</p>
      </b-col>
      <b-col
        v-show="profile !== null"
        cols="12"
        class="d-flex justify-content-center"
      >
        <b-avatar
          v-if="profile !== null"
          :src="profile.pictureUrl"
          size="6rem"
        ></b-avatar>
      </b-col>
      <b-col
        v-show="profile !== null"
        cols="12"
        class="d-flex justify-content-center"
      >
        <p v-if="profile !== null">
          {{ profile.displayName }} >> {{ profile.statusMessage }}
        </p>
      </b-col>
    </b-row>
    
     
    <b-row class="m-0" >
      <b-col cols="10" class="d-flex justify-content-center">
        <b-form-input
          v-model="text"
          type="text"
          placeholder="ส่งข้อความอะไรสักอย่าง..."
          required
        ></b-form-input>
      </b-col>
      <b-col cols="2" class="d-flex justify-content-center">
        <b-button
          :disabled="text == '' ? true : false"
          pill
          variant="info"
          @click="send()"
          >ส่ง</b-button
        ></b-col
      ></b-row
    >

    <b-row class="m-0">
      <!-- <b-col cols="12" class="m-2">
        <b-button block pill variant="success" @click="afterlogin()"
          >get data after login</b-button
        ></b-col
      > -->
      <b-col cols="12" class="m-2" v-show="isInClient == false">
        <b-button block pill variant="danger" @click="logout()"
          >log out</b-button
        ></b-col
      ></b-row
    >
  </div>
</template>

<script>
export default {
  name: "Home",
  components: {},
  data() {
    return {
      // liff: null,
      text: "",
      os: null,
      language: null,
      version: null,
      isInClient: null,
      profile: null,
      login: false,
      context: null,
    };
  },
  created() {
    this.startline();
  },
  watch: {
    login: async function () {
      if (this.login) {
        await this.client();
      }
    },
  },
  mounted() {},
  methods: {
    async getContext() {
      this.context = await this.$liff.getContext();
    },
    async send() {
      if (this.text !== "") {
        console.log(this.text);
        await this.$liff
          .sendMessages([
            {
              type: "sticker",
              stickerId: 1,
              packageId: 1,
            },
            {
              type: "text",
              text: String(this.text),
            },
          ])
          .then(() => {
            console.log("message sent");
            this.text = "";
          })
          .catch((err) => {
            console.log("error", err);
          });
      }
    },
    async logout() {
      console.log("---------------------logout-----------------");
      if (this.$liff.isLoggedIn()) {
        this.$liff.logout();
        location.reload();
      }
    },
    async startline() {
      // 1656334774-3K4X1g0G
      await this.$liff.init({ liffId: "1656334774-3K4X1g0G" });
      console.log("------------startline----------------");
      console.log(this.$liff.isLoggedIn());

      if (!this.$liff.isLoggedIn()) {
        await this.$liff.login({});
        // this.login = await this.$liff.login({});
        await this.client();
        await this.afterlogin();
      } else {
        // this.login = await this.$liff.login({});
        await this.client();
        await this.afterlogin();
      }
    },
    async afterlogin() {
      await this.gversion();
      // await this.client();
      await this.Os();
      await this.Language();
      await this.getProfile();
      await this.getContext();
    },
    async Os() {
      console.log("----------------Os-----------------");
      if (this.$liff.isLoggedIn() && this.$liff.isInClient()) {
        console.log(`isLoggedIn : ${this.$liff.isLoggedIn()}`);
        try {
          this.os = await this.$liff.getOS();
        } catch (error) {
          console.log(error);
        }
      }
    },
    async Language() {
      console.log("----------------Language-----------------");
      if (this.$liff.isLoggedIn() && this.$liff.isInClient()) {
        console.log(`isLoggedIn : ${this.$liff.isLoggedIn()}`);
        try {
          this.language = await this.$liff.getLanguage();
        } catch (error) {
          console.log(error);
        }
      }
    },
    async gversion() {
      console.log("----------------gversion-----------------");
      if (this.$liff.isLoggedIn()) {
        console.log(`isLoggedIn : ${this.$liff.isLoggedIn()}`);
        try {
          this.version = await this.$liff.getVersion();
        } catch (error) {
          console.log(error);
        }
      }
    },
    async client() {
      console.log("----------------client-----------------");
      if (this.$liff.isLoggedIn()) {
        console.log(`isLoggedIn : ${this.$liff.isLoggedIn()}`);
        try {
          this.isInClient = await this.$liff.isInClient();
        } catch (error) {
          console.log(error);
        }
      }
    },
    async getProfile() {
      console.log("----------------getProfile-----------------");
      if (this.$liff.isLoggedIn()) {
        console.log(`isLoggedIn : ${this.$liff.isLoggedIn()}`);
        this.$liff
          .getProfile()
          .then((profile) => {
            this.profile = profile;
          })
          .catch((err) => {
            console.log("error", err);
          });
      }
    },
  },
};
</script>
